<template>
    <div id="cart">
        <div class="wrapper">
            <div class="sidebar">
                <div class="sidebar-link">
                    <div class="sidebar-link__btn">
                        <router-link :to="{name: 'home'}">
                            <button class="btn-home"></button>
                        </router-link>
                    </div>
                    <div class="sidebar-link__btn">
                        <button class="btn-retry"
                                @click="reloadPage()">
                        </button>
                    </div>
                </div>
                <div class="parameters">
                    <p>Параметры:</p>
                    <div class="parameters__counter">
                        <div class="parameters__counter-inm">
                            <div class="parameters__counter-inner background-sad"></div>
                            <span class="parameters__counter-number">{{sadCounter}}</span>
                        </div>
                        <div class="parameters__counter-inm">
                            <div class="parameters__counter-inner background-happy"></div>
                            <span class="parameters__counter-number">{{happyCounter}}</span>
                        </div>
                        <div class="parameters__counter-inm">
                            <div class="parameters__counter-inner background-heart"></div>
                            <span class="parameters__counter-number">{{loyalCounter}}</span>
                        </div>
                    </div>
                </div>
                <div class="queue">
                    <p>Осталось в очереди:</p>
                    <p class="queue__bolt">{{users.length}}/<span>{{counter}}</span></p>
                </div>
            </div>

            <div class="card-side">
                <div class="choice">
                    <div class="happy-up">
                        <div class="parameters__counter-inner background-happy"></div>
                        <div class="parameters__counter-inner background-swipe-up"></div>
                    </div>
                    <div class="parameters__counter-inner background-sad"></div>
                    <div class="parameters__counter-inner background-swipe-left"></div>
                    <div class="parameters__counter-inner background-heart"></div>
                    <div class="parameters__counter-inner background-swipe-right"></div>
                </div>

                <div class="card"
                     :style="onSwipe"
                     @touchstart="handleTouchStart($event)"
                     @touchmove="handleTouchMove($event)">
                    <div class="card__img">
                        <img :src="users[0].img" alt="img">
                        <div class="img-overlay"></div>
                        <div class="card-sale color-sad"
                             v-if="isSad">
                            Препарат 1
                        </div>
                        <div class="card-sale color-happy"
                             v-if="isHappy">
                            Препарат 2
                        </div>
                        <div class="card-sale color-loyal"
                             v-if="isLoyal">
                            Препарат 3
                        </div>
                    </div>
                    <div class="card__inner">
                        <div class="card-name">{{users[0].name}}</div>
                        <div class="card-message">{{users[0].commentMessage}}</div>
                    </div>
                </div>
                <div class="card-side__buttons" @click="addToCart($event)">
                    <button class="btn-1" value="1">Препарат 1</button>
                    <button class="btn-1" value="2">Препарат 2</button>
                    <button class="btn-1" value="3">Препарат 3</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Cart.vue",
        data() {
            return {
                /*windowWidth: window.innerWidth,
                changeNameClass: '',*/

                counter: '',
                sadCounter: 0,
                happyCounter: 0,
                loyalCounter: 0,
                isSad: false,
                isHappy: false,
                isLoyal: false,
                touchLeft: {
                    left: '-150%',
                    transition: 'all 1.5s',
                    transform: 'rotate(-15deg)'
                },
                touchRight: {
                    left: '200%',
                    transition: 'all 1.5s',
                    transform: 'rotate(15deg)'
                },
                touchUp: {
                    top: '-100%',
                    transition: 'all 1.5s',
                    transform: 'rotate(-15deg)'
                },
                onSwipe: {transition: 'all 0s'},
                xDown: "null",
                yDown: "null",
                users: [
                    {
                        img: require('../assets/images/1-Smart.jpg'),
                        name: "Марія, 19 років",
                        commentMessage: "Бабуся приймає брендовий препарат від болю в суглобах, він допомагає, але занадто дорогий. У Вас є якісний аналог з нижчою ціною? Якщо ні — давайте бренд.",
                    },
                    {
                        img: require('../assets/images/2-Poor.jpg'),
                        name: "Степан, 61 рік",
                        commentMessage: "Спросоння відсунув гарячий чайник рукою та обпікся. У Вас всі ліки від опіків такі дорогі? Можна хороший препарат недорого?",
                    },
                    {
                        img: require('../assets/images/3-Smart.jpg'),
                        name: "Любов, 58 років",
                        commentMessage: "Лікар призначив препарат від артеріальної гіпертензії, а бренд дорого коштує. Тому мені потрібен аналог з хорошою ефективністю та приємною ціною.",
                    },
                    {
                        img: require('../assets/images/4-Smart.jpg'),
                        name: "Олександр, 20 років",
                        commentMessage: "Порекомендуйте ефективний препарат від болю в горлі за розумну ціну.",
                    },
                    {
                        img: require('../assets/images/5-Smart.jpg'),
                        name: "Ірина, 55 років",
                        commentMessage: "У мене часто невралгії, лікар призначив вітаміни групи В. Мені потрібен якісний аналог за прийнятною ціною.",
                    }
                ],
            }
        },
        methods: {
            reloadPage() {
                window.location.reload();
            },
            addToCart(event) {
                if (event.target.value === "1") {
                    this.sadCounter++;
                } else if (event.target.value === "2") {
                    this.happyCounter++;
                } else if (event.target.value === "3") {
                    this.loyalCounter++;
                }
                if (event) {
                    this.showCard()
                }
            },
            showCard() {
                this.users.splice(0, 1);
                if (this.users.length === 0) {
                    this.$router.push({
                        name: 'final',
                        params: {
                            getPercentSad: this.sadCounter,
                            getPercentHappy: this.happyCounter,
                            getPercentLoyal: this.loyalCounter
                        }
                    });
                }
            },
            handleTouchStart(evt) {
                this.xDown = evt.touches[0].clientX;
                this.yDown = evt.touches[0].clientY;
            },
            handleTouchMove(evt) {
                if (!this.xDown || !this.yDown) {
                    return;
                }

                let xUp = evt.touches[0].clientX;
                let yUp = evt.touches[0].clientY;

                let xDiff = this.xDown - xUp;
                let yDiff = this.yDown - yUp;
                // немного поясню здесь. Тут берутся модули движения по оси абсцисс и ординат (почему модули? потому что если движение сделано влево или вниз, то его показатель будет отрицательным) и сравнивается, чего было больше: движения по абсциссам или ординатам. Нужно это для того, чтобы, если пользователь провел вправо, но немного наискосок вниз, сработал именно коллбэк для движения вправо, а ни как-то иначе.
                if (Math.abs(xDiff) > Math.abs(yDiff)) {/*most significant*/
                    if (xDiff > 0) {
                        this.onSwipe = this.touchLeft;
                        this.isSad = true;
                        this.sadCounter++;
                        setTimeout(() => this.showCard(), 900);
                        setTimeout(() => this.onSwipe = 0, 1000);
                        setTimeout(() => this.isSad = false, 1000);
                        /* left swipe */
                    } else {
                        // console.log("right")
                        this.onSwipe = this.touchRight;
                        this.isLoyal = true;
                        this.loyalCounter++;
                        setTimeout(() => this.showCard(), 900);
                        setTimeout(() => this.onSwipe = 0, 1000);
                        setTimeout(() => this.isLoyal = false, 1000);
                        /* right swipe */
                    }
                } else { // Это вам, в общем-то, не надо, вы ведь только влево-вправо собираетесь двигать
                    if (yDiff > 0) {
                        this.onSwipe = this.touchUp;
                        this.isHappy = true;
                        this.happyCounter++;
                        setTimeout(() => this.showCard(), 900);
                        setTimeout(() => this.onSwipe = 0, 1000);
                        setTimeout(() => this.isHappy = false, 1000);
                        // console.log("up")
                        /* up swipe */
                    } else {
                        /* down swipe */
                    }
                }
                /* reset values */
                this.xDown = null;
                this.yDown = null;
            }
        },
        mounted() {
            this.counter = this.users.length;
            /*window.onresize = () => {
                if (window.innerWidth <=375){
                    this.changeNameClass = 'mobile'
                } else if ( 376 < window.innerWidth && window.innerWidth < 812){
                    this.changeNameClass = 'phablet'
                } else {
                    this.changeNameClass = ''
                }
                console.log("1-" + this.windowWidth);
                console.log("2-" + window.innerWidth);
                // this.windowWidth = window.innerWidth
            }*/
        },
    }

</script>

<style scoped>

    #cart {
        position: fixed;
        width: 100%;
        height: 100%;
    }

    img {
        max-width: 100%;
    }

    .wrapper {
        display: flex;
        height: 100%;
    }

    .sidebar {
        width: 25vw;
        background-image: url("../assets/images/pink_bg.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .sidebar-link {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .sidebar-link__btn button {
        width: 5vw;
        height: 5vw;
        border: none;
        border-radius: 100%;
        margin-top: 2vw;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        cursor: pointer;
    }

    .btn-home {
        background-image: url("../assets/images/btn_home.jpg");
    }

    .btn-retry {
        background-image: url("../assets/images/btn_retry.jpg");
    }

    .parameters {
        padding: 0 30px;
        max-width: 200px;
    }

    .parameters p {
        font-size: 2vw;
        letter-spacing: 0.03em;
        color: #FFFFFF;
    }

    .parameters__counter {
        margin-top: 1vw;
    }

    .parameters__counter-inm {
        margin-bottom: 1vw;
        padding: 0.3vw;
        background: #FFFFFF;
        box-shadow: 0 0 20px rgb(131 42 64 / 40%);
        border-radius: 100px;
        font-weight: 700;
        font-size: 2.2vw;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #424242;
    }

    .parameters__counter-inner {
        width: 3vw;
        height: 3vw;
        border: none;
        border-radius: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        cursor: pointer;
    }

    .background-sad {
        background-image: url("../assets/images/sad_icon.jpg");
    }

    .background-swipe-up {
        background-image: url("../assets/images/swipe-up.png");
        min-width: 41px;
        min-height: 41px;
    }

    .background-happy {
        background-image: url("../assets/images/happy_icon.jpg");
    }

    .background-swipe-left {
        background-image: url("../assets/images/swipe-left.png");
    }

    .background-heart {
        background-image: url("../assets/images/heart_icon.jpg");
    }

    .background-swipe-right {
        background-image: url("../assets/images/swipe-right.png");
    }

    .queue {
        width: 100%;
        height: 8vw;
        font-size: 2vw;
        padding: 5px;
        background: #ffffff4f;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        letter-spacing: 0.05em;
    }

    .queue p {
        color: #FFFFFF;
    }

    .queue__bolt {
        font-size: 3vw;
        font-weight: 700;
    }

    .queue__bolt span {
        font-weight: 200;
    }

    .card-side {
        width: 75vw;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
    }

    .choice {
        display: none;
    }

    @media screen and (max-device-width: 1366px) {
        .choice {
            display: block;
        }
    }

    .choice .happy-up {
        display: flex;
        justify-content: center;
        margin-top: 5px;
    }

    .choice .background-sad {
        position: absolute;
        top: 45%;
        margin-left: 4%;
    }

    .choice .background-swipe-left {
        position: absolute;
        top: 52%;
        margin-left: 3%;
        min-width: 41px;
        min-height: 41px;
    }

    .choice .background-heart {
        position: absolute;
        top: 45%;
        right: 4%;
    }

    .choice .background-swipe-right {
        position: absolute;
        top: 52%;
        right: 3%;
        min-width: 41px;
        min-height: 41px;
    }

    .card {
        position: absolute;
        width: 22vw;
        min-width: 119px;
        background: #FFFFFF;
        box-shadow: 0 0 40px rgb(127 127 127 / 40%);
        border-radius: 2vw;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -43%);
    }

    .card__img img {
        width: 100%;
        border-radius: 2vw 2vw 0 0;
    }

    .img-overlay {
        position: absolute;
        width: 100%;
        /*height: 388px;*/
        height: 100%;
        background: linear-gradient(
                63.53deg, #2D8550 16.62%, #5E6EC2 83.38%);
        opacity: 0.2;
        top: 0;
        right: 0;
        border-radius: 2vw;
    }

    .card-sale {
        position: absolute;
       /* width: 360px;
        height: 120px;*/
        width: 50vw;
        height: 16vw;
        left: 50%;
        top: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 4vw;
        font-weight: 700;
        z-index: 5;
    }

    .color-sad {
        border: 2vw solid #8049C7;
        color: #8049C7;
        transform: translate(-50%, -7%) rotate(-30deg);
    }

    .color-happy {
        border: 2vw solid #169AE4;
        color: #169AE4;
        transform: translate(-50%, -7%) rotate(-30deg);
    }

    .color-loyal {
        border: 2vw solid #FFB903;
        color: #FFB903;
        transform: translate(-50%, -7%) rotate(30deg);
    }

    .card__inner {
        padding: 1vw 2vw;
        margin-top: -5px;
        background-color: #FFFFFF;
        border-radius: 0 0 2vw 2vw;
        position: relative;
    }

    .card-name {
        font-size: 2vw;
        letter-spacing: 0.03em;
        font-weight: 700;
        color: #8E9AD5;
    }

    .card-message {
        font-size: 1vw;
        line-height: 1.3vw;
        letter-spacing: 0.03em;
        margin-top: 1vw;
        opacity: 0.6;
    }

    .card-side__buttons {
        display: flex;
        justify-content: space-around;
        align-items: flex-end;
        height: 100%;
    }

    .card-side__buttons button {
        width: 260px;
        height: 56px;
        border: none;
        border-radius: 100px;
        font-size: 30px;
        letter-spacing: 0.03em;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #FFFFFF;
        margin: 25px;
    }

    .card-side__buttons button:nth-child(1) {
        background: linear-gradient(266.19deg, #8049C7 0%, #CA57FD 100%);
    }

    .card-side__buttons button:nth-child(2) {
        background: linear-gradient(266.19deg, #169AE4 0%, #0CC4FA 100%);
    }

    .card-side__buttons button:nth-child(3) {
        background: linear-gradient(90deg, #FFD748 0.02%, rgba(195, 199, 11, 0.96) 99.97%, #CAC6AB 99.98%, #D3E9E1 99.99%);
    }

    @media screen and (max-device-width: 1366px) {
        .card-side__buttons {
            display: none;
        }
    }
</style>

