                    <button class="btn-home"></button>
                </router-link>
            </div>

            <div class="inner">
                <div class="inner__statistics">
                    <div class="statistics">
                        <p class="statistics__percent">{{get(getPercentSad)}}</p>
                        <p class="statistics__text">Препарат 1</p>
