<script>
    export default {
        name: "Final.vue",
        props: [
            "getPercentSad",
            "getPercentHappy",
            "getPercentLoyal",
        ],
        methods: {
            get(x) {
                let sum = this.getPercentSad + this.getPercentHappy + this.getPercentLoyal;
                return Math.round((x * 100) / sum) + "%";
            }
        },
    }
</script>

