import VueRouter from 'vue-router'
import Home from './components/Home'
import Cart from './components/Cart'
import Final from './components/Final'

export default new VueRouter({
    routes: [

        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/cart',
            name: 'cart',
            component: Cart,
        },
        {
            path: '/final',
            name: 'final',
            component: Final,
            props: true   // прописываем для передачи сюда данных
        }
    ],
    // mode: 'history'

})